* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-size: 62.5%;
  background-color: #023047;
}

h1 {
  border: 3px solid black;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #023047;
  color: #ffb703;
  margin-top: 1rem;
  margin-bottom: 1rem;
  padding: 0.7rem;
}

.App {
  font-family: sans-serif;
  /* text-align: center; */
  /* display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; */

  /* height: 20rem; */
  margin: 2rem;

  padding: 1rem;
}

.input-el {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 1rem;
}

.container {
  background-color: #ffb703;
  border: 2px solid black;
  border-radius: 11px;
  padding: 1rem;
}

label {
  font-size: 1.2rem;
  font-weight: 600;
}

input {
  border-radius: 9px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  margin-bottom: 0.5rem;

}

h2 {
  display: flex;
  justify-content: center;
  align-items: center;
}


.input {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.output {
  font-size: 2rem;
}

.result {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 9px;
  border: 3px solid black;
  margin: 2.4rem auto;
  padding: 1rem;
  background-color: #023047;
  color: #ffb703;

}


.display-output {
  display: flex;
  padding: 0.5rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

button {
  display: inline-block;
  border-radius: 3px;
  border: 2px solid black;
  padding: 0.6rem;
  font-weight: 600;
  background-color: #ffb703;


}

.btn {
  /* display: flex;
  justify-content: center;
  align-items: center; */
  background-color: #023047;
  color: #ffb703;

}

footer {
  font-size: 1rem;
  font-weight: 600;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;


}

@media (max-width:34em) {
  body {
    font-size: 45%;
  }

  input {
    padding: 0.5rem;
  }

  label {
    font-size: 0.8rem;
  }

  footer {
    font-size: 0.7rem;
    font-weight: 700;

  }

  button {
    font-size: 0.7rem;
  }
}